$red: #FF3C3C;
$bezier: cubic-bezier(.17, .67, 0, 1);

@function grid($grid-size) {
	@return calc(100vw / 24 * #{$grid-size});
}

@font-face {
	font-family: "Anybody";
	src: url("https://assets.codepen.io/1492907/Anybody.woff2") format("woff2");
}


* {
	font-family: "Anybody";
	margin: 0;
	box-sizing: border-box;
	color: #252525;
}